import React, { Component } from "react";
import axios from "axios";

class App extends Component {
  state = {
    city: "",
    currentTemp: "0",
    minTemp: "0",
    maxTemp: "0",
    humidity: "0",
    overcast: "...",
    error: "",
  };
  getWeather = (cityName) => {
    axios
      .get(
        "https://cors-anywhere.herokuapp.com/http://api.openweathermap.org/data/2.5/weather?q=" +
          cityName +
          "&units=metric&APPID=e9e90ff9ce6854e5890726c2fbffbe93"
      )
      .then((response) => {
        this.setState({
          currentTemp: response.data.main.temp + " C",
          minTemp: response.data.main.temp_min + " C",
          maxTemp: response.data.main.temp_max + " C",
          pressure: response.data.main.pressure + " C",
          humidity: response.data.main.humidity + "%",
          overcast: response.data.weather[0].description,
          error: "",
        });
      })
      .catch((error) => {
        console.log(error);
        this.setState({
          error: "City not found!",
        });
      });
  };
  Click = (e) => {
    e.preventDefault();
    this.getWeather(this.state.city);
  };

  render() {
    return (
      <React.Fragment>
        <form autoComplete="off">
          <input
            type="text"
            placeholder=" City"
            value={this.state.city}
            onChange={(e) => this.setState({ city: e.target.value })}
          />
          <button id="enter" onClick={(e) => this.Click(e)}>
            Enter
          </button>
        </form>
        <ul>
          <li>{this.state.error}</li>
          <li>Temperature</li>
          <li className="Temp">{this.state.currentTemp}</li>
          <li>Maximum Temperature</li>
          <li className="MaxT">{this.state.maxTemp}</li>
          <li>Minimun Temperature</li>
          <li className="MinT">{this.state.minTemp}</li>
          <li>Humidity</li>
          <li className="Humidity">{this.state.humidity}</li>
          <li>condition</li>
          <li className="OverCast">{this.state.overcast}</li>
        </ul>
      </React.Fragment>
    );
  }
}

export default App;
